export class ListaPilotos {

    constructor() {
        
        this._pilotos = [];
    }

    get pilotos() {
        
        return [].concat(this._pilotos);
    }
    
    adiciona(piloto) {
        
        this._pilotos.push(piloto);
    }

    esvazia() {
        
        this._pilotos = [];
    }

    ordena(criterio) {

        this._pilotos.sort(criterio);        
    }
    
    inverteOrdem() {

        this._pilotos.reverse();
    }    
}